@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;800&display=swap);
.bio{color:#000000;margin:0 auto}.bio__name{font-size:46px;line-height:70px;color:#000000;margin-bottom:24px}@media (min-width: 767px){.bio__name{font-size:72px}}.bio__name--heavy{font-weight:bold;color:#ffffff;background-color:black;padding:8px 24px;text-transform:uppercase}.bio__occupation{color:#b0b0b0;margin-bottom:72px;font-size:18px;text-align:left}@media (min-width: 767px){.bio__occupation{font-size:32px}}.bio__information-icon{width:30px;vertical-align:middle;margin-right:4px}.bio__information-socials{font-size:18px;text-align:center}@media (min-width: 767px){.bio__information-socials{font-size:24px}}.bio__information-socials *+*{margin-left:48px}.bio__information-socials-link{color:black;text-decoration:none}

.homepage{width:100vw;height:100vh;background-color:#f2f2f2;display:flex;justify-self:center;align-items:center;padding:24px;box-sizing:border-box}

body {
  margin: 0;
  padding: 0;
  font-family: 'Inter', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p, h1, h2, h3, h4, ul, li {
  -webkit-margin-before: 0em;
          margin-block-start: 0em;
  -webkit-margin-after: 0em;
          margin-block-end: 0em;
}
